import { useCallback, useEffect, useState } from "react";
import {
  Flex,
  Image,
  Text,
  useToast,
  useMediaQuery,
  List,
  ListItem,
  Button,
  CloseButton,
} from "@chakra-ui/react";
import Timer from "./Timer";
import axios from "axios";
import { useMoralis, useChain } from "react-moralis";

import ConnectWalletDrawer from "./ConnectWalletDrawer";
import BuyToken from "./BuyToken";
import {
  BIGNFT_PRICE_USD_PHASE_ONE,
  NFT_TOKEN_SYMBOL,
} from "../CONTRACT_DETAILS";

import { ONRAMP_SRC_URL } from "../ONRAMPER_DETAILS";

import { ref } from "firebase/database";
import { useObject } from "react-firebase-hooks/database";

import { CHAINS_SUPPORTED } from "../CHAINS_SUPPORTED";
import ProgressBar1 from "./ProgressBar1";

import coinBNB from "../assets/coinBNB.svg";
import coinETH from "../assets/coinETH.png";
import { db, firebaseRootItem } from "../firebaseConfig";

function Hero({
  login,
  setReRender,
  reRender,
  firebaseApp,
  currentPhase,
  currentChainIsSupported,
}) {
  /**
   * TOAST : START
   */
  const toast = useToast();

  const showErrToast = useCallback(
    (errTitle, errMsg) => {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: errTitle,
        description: errMsg ? errMsg : "",
        status: "error",
        duration: 3000,
        isClosable: true,
      });
    },
    [toast]
  );
  const showInfoToast = useCallback(
    (errTitle, errMsg) => {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: errTitle,
        description: errMsg ? errMsg : "",
        status: "info",
        duration: 3000,
        isClosable: true,
      });
    },
    [toast]
  );
  // const showSuccessToast = useCallback(
  //   (errTitle, errMsg) => {
  //     toast({
  //       containerStyle: {
  //         maxWidth: "92vw",
  //       },
  //       title: errTitle,
  //       description: errMsg ? errMsg : "",
  //       status: "success",
  //       duration: 3000,
  //       isClosable: true,
  //     });
  //   },
  //   [toast]
  // );
  /**
   * TOAST : END
   */

  const setNativeValue = useCallback((element, value) => {
    const valueSetter = Object.getOwnPropertyDescriptor(element, "value").set;
    const prototype = Object.getPrototypeOf(element);
    const prototypeValueSetter = Object.getOwnPropertyDescriptor(
      prototype,
      "value"
    ).set;

    if (valueSetter && valueSetter !== prototypeValueSetter) {
      prototypeValueSetter.call(element, value);
    } else {
      valueSetter.call(element, value);
    }
  }, []);

  const [isLargerThan500] = useMediaQuery("(min-width: 600px)");

  const { isAuthenticated, chainId, isWeb3Enabled } = useMoralis();
  const { chain, switchNetwork } = useChain();

  /**
   * START - 143
   * get geolocation of user
   */

  const [isIndianUser, setIsIndianUser] = useState(false);

  const getGeoInfo = () => {
    axios
      .get("https://ipapi.co/json/")
      .then((response) => {
        let country = response.data.country_name + "";
        country = country.toLowerCase();
        country === "india" && setIsIndianUser(true);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    if (!isAuthenticated) return;
    getGeoInfo();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isAuthenticated]);

  /**
   * END - 143
   * get geolocation of user
   */

  const [onRampVisibility, setonRampVisibility] = useState(false);
  const toggleCryptoOnRamp = useCallback(() => {
    const onRampIframe = document.getElementById("fiatOnRampiFrame");
    let userAddress = localStorage.getItem("NFT_userAddress");
    let nativeC = chain?.nativeCurrency.symbol.toUpperCase();
    let queryParams = `&defaultCrypto=${nativeC}&wallets=${nativeC}:${userAddress}`;
    let newURL = ONRAMP_SRC_URL + queryParams;
    if (isIndianUser) {
      let onRampNetwork = "bep20";
      if (chain?.chainId === "0x1") {
        onRampNetwork = "erc20";
      }
      newURL = `https://onramp.money/main/buy/?appId=1&walletAddress=${userAddress}&coinCode=${nativeC}&network=${onRampNetwork}`;
    }

    console.log(`newURL = ${newURL}`);

    onRampIframe.setAttribute("src", newURL);
    console.log();
    const onRampEl = document.getElementById("fiatOnRamp");
    if (!onRampVisibility) {
      // onRampEl.style.display = "flex";
      onRampEl.style.height = "100vh";
      onRampEl.style.padding = "1rem";
      // onRampEl.style.scale = "1";
      setonRampVisibility(true);
    } else {
      // onRampEl.style.scale = "0";
      onRampEl.style.height = "0vh";
      onRampEl.style.padding = "0rem";

      // onRampEl.style.display = "none";
      setonRampVisibility(false);
    }
  }, [onRampVisibility, chain, isIndianUser]);

  /**
   * START
   * Checks from firebase if the presaleIsOpen or not
   */
  const [isPresaleOpenDB, isPresaleOpenDB_loading] = useObject(
    ref(db, `${firebaseRootItem}/batsPrivatesaleIsOpen`)
  );
  const [isPresaleOpen, setIsPresaleOpen] = useState(false);
  useEffect(() => {
    if (isPresaleOpenDB_loading) return;
    setIsPresaleOpen(isPresaleOpenDB.val());
  }, [isPresaleOpenDB_loading, isPresaleOpenDB]);
  /**
   * END
   * Checks from firebase if the presaleIsOpen or not
   */

  return (
    <>
      <Flex
        direction={"column"}
        color="#DDCDEA"
        justifyContent="center"
        border="4px solid rgba(255, 255, 255, 0.2)"
        borderRadius={"16px"}
        background="linear-gradient(90deg, rgba(52, 18, 64, 0.5) 0%, rgba(0, 31, 78, 0.5) 51.04%, rgba(35, 20, 68, 0.5) 100%)"
        padding={"2rem .5rem"}
        backdropFilter="blur(5px)"
        maxWidth={isLargerThan500 ? "650px" : "100%"}
        width={"100%"}
        id="presale-block"
        overflow={"hidden"}
      >
        {isWeb3Enabled && isAuthenticated && (
          <Flex
            fontSize={"small"}
            marginTop="-2rem"
            justifyContent={"center"}
            padding=".3em"
            paddingBottom={"1rem"}
            gap="1rem"
          >
            <Text>
              Currently on{" "}
              <Image
                marginLeft={".5rem"}
                marginRight={".25rem"}
                src={chainId === "0x1" ? coinETH : coinBNB}
                width="1rem"
                marginTop={".1rem"}
                height={"auto"}
                display="inline-block"
                position={"relative"}
                top="0.2rem"
              />{" "}
              {chain?.name}
            </Text>
            <Button
              onClick={() =>
                switchNetwork(`${chainId === "0x1" ? "0x38" : "0x1"}`)
              }
              fontSize="smaller"
              height={"max-content"}
              variant="outline"
              marginTop={".2rem"}
              padding=".2rem .5rem"
            >
              Switch Chain
            </Button>
          </Flex>
        )}
        <Text
          as="h1"
          textTransform={"uppercase"}
          fontWeight="black"
          fontSize={isLargerThan500 ? "2rem" : "1.3rem"}
          fontFamily={"Russo One"}
          className="gradient-text"
          textAlign={"center"}
          width="full"
        >
          Buy {NFT_TOKEN_SYMBOL} on presale
        </Text>
        <Text
          textTransform={"uppercase"}
          fontSize={".8rem"}
          fontFamily={"Russo One"}
        >
          Phase {currentPhase}
        </Text>
        <Text
          as="h2"
          textTransform={"uppercase"}
          fontSize={isLargerThan500 ? "1.2rem" : "1rem"}
        >
          1 {NFT_TOKEN_SYMBOL} ={" $"}
          {BIGNFT_PRICE_USD_PHASE_ONE}
        </Text>
        <Text
          textTransform={"uppercase"}
          fontSize={".8rem"}
          fontFamily={"Russo One"}
          paddingTop={".3rem"}
        >
          {currentPhase !== "3"
            ? "PRICE INCREASES IN NEXT PHASE"
            : "LAST CHANCE TO BUY"}
        </Text>

        <ProgressBar1
          currentPhase={currentPhase}
          firebaseApp={firebaseApp}
          isLargerThan500={isLargerThan500}
        />
        <Timer isLargerThan500={isLargerThan500} currentPhase={currentPhase} />

        {!isAuthenticated ? (
          <Flex
            gap={"1rem"}
            margin=".5rem auto"
            marginBottom={"1rem"}
            direction={isLargerThan500 ? "row" : "column"}
          >
            <ConnectWalletDrawer
              login={login}
              currentChainIsSupported={currentChainIsSupported}
            />
            {/* <Link to="/how-to-buy-bats">
              <Flex
                textTransform="uppercase"
                fontWeight={"black"}
                borderRadius="4px"
                boxShadow={"2px 2px 23px rgba(0, 0, 0, 0.3)"}
                padding=".75rem 1.5rem"
                background={"#ffffff14"}
                height="2.5rem"
                alignItems={"center"}
                _hover={{
                  background: "#ffffff29",
                }}
              >
                <Image src={iconHelp} marginRight=".5rem" />
                How to Buy
              </Flex>
            </Link> */}
          </Flex>
        ) : (
          ""
        )}
        {/* @todo - buying shouldn't be possible after timer is up */}
        {isAuthenticated && !currentChainIsSupported && (
          <>
            <Flex direction={"column"} gap={"1rem"}>
              <Text
                as={"h4"}
                background="#FEB2B229"
                borderRadius={"md"}
                textTransform={"uppercase"}
                fontSize="1.5rem"
                fontWeight={"black"}
                color={"#FEB2B2"}
              >
                Unsupported Chain <br />
                {chain?.name}
              </Text>
              <Text>Click on your wallet address (top-right) to change</Text>
              <List textAlign={"left"}>
                <Text as={"h5"} textAlign={"left"} fontSize="1.3rem">
                  Supported Chains
                </Text>
                {CHAINS_SUPPORTED.map((e) => (
                  <ListItem key={e?.shortName}>
                    {e.shortName.toUpperCase()} - {e.fullName} ({e.chainID})
                  </ListItem>
                ))}
              </List>
            </Flex>
          </>
        )}
        {isAuthenticated && currentChainIsSupported ? (
          <BuyToken
            toast={toast}
            showErrToast={showErrToast}
            showInfoToast={showInfoToast}
            isLargerThan500={isLargerThan500}
            setReRender={setReRender}
            reRender={reRender}
            currentPhase={currentPhase}
            setNativeValue={setNativeValue}
            currentChainIsSupported={currentChainIsSupported}
            toggleCryptoOnRamp={toggleCryptoOnRamp}
            isPresaleOpen={isPresaleOpen}
            isIndianUser={isIndianUser}
          />
        ) : (
          ""
        )}
      </Flex>

      {/* THE CRYPTO ONRAMP WHICH IS HIDDEN BY DEFAULT */}
      <Flex
        width={"100vw"}
        height="0vh"
        scale={"0"}
        padding={"0rem"}
        transition={"all 0.2s ease-in"}
        background="#000000e3"
        backdropFilter={"blur(50px)"}
        justifyContent={"flex-start"}
        alignItems={"center"}
        gap={"1rem"}
        id="fiatOnRamp"
        direction={"column"}
        position={"fixed"}
        bottom={"0"}
        left={"0rem"}
        zIndex={"30"}
      >
        <CloseButton
          size={"lg"}
          marginLeft={"auto"}
          onClick={toggleCryptoOnRamp}
        />
        <iframe
          height="625"
          id="fiatOnRampiFrame"
          title="Fiat OnRamp Widget"
          src={ONRAMP_SRC_URL}
          allowtransparency="true"
          style={{
            width: "100%",
            maxHeight: "625px",
            maxWidth: "500px",
          }}
        ></iframe>
        <Button width={"16rem"} onClick={toggleCryptoOnRamp}>
          Close
        </Button>
      </Flex>
    </>
  );
}

export default Hero;
